.movie {
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 1fr;
  grid-gap: 20px;
  text-decoration: none;
  color: inherit;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__img {
  position: relative;
  top: 0px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__title,
.movie__year {
  margin: 0;
  font-weight: 300;
  text-decoration: none;
}

.movie__title a {
  margin-bottom: 5px;
  font-weight: bold;
  font-size: 24px;
  color: #2c2c2c;
  text-decoration: none;
}

.movie__genres {
    background-color: #adaeb9;
    justify-content: center;
    list-style: none;
    padding: 10px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    border-radius: 10px;
}

.movie__genres li,
.movie__year {
  margin-right: 10px;
  font-size: 14px;
}
@media screen and (max-width: 1100px) {
  .movie {
    grid-template-columns: 1fr;
    width: 100%;
  }
}